import {
    BrowserRouter as Router,
    Switch,
    Route,
} from "react-router-dom";
import React from "react";

function App() {
    return (
        <Router basename={'/spark'} style={{overflow: "hidden"}}>
            <Switch>
                <Route exact path="/">
                    <iframe src={"https://purdue-ecess.github.io/spark-website/"}
                            style={{width: '100%', height: 'calc(100vh - 10px)'}}/>
                </Route>
                <Route path="/schedule">
                    <iframe src={"https://purdue-ecess.github.io/spark-website/interactive-schedule/"}
                            style={{width: '100%', height: 'calc(100vh - 10px)'}}/>
                </Route>
            </Switch>
        </Router>
    );
}

export default App;
